body {
    background: black;
    background-image: url(/static/media/strom.06f13b27.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
}

body.game {
    background: #333;
    /*background-image: url("../img/escheresque_ste.png");*/

    background-repeat: repeat;
}

#chatdiv p.colored,
#chatdiv p.colored b {
    color: #f442f4;
}

#chat-text p {
    margin: 2px;
}

#message-area {
    color: red;
    font-weight: bold;
    display: block;
    margin-top: 10px;
}

#chatdiv {
    border-radius: 5px;
    color: white;
    width: 100%;
    max-height: 200px;
    background: rgba(0, 0, 0, 0.4);
    padding: 10px;
}

#chat {
    position: absolute;
    left: 10px;
    bottom: 10px;
    width: 400px;
}

#position {
    position: absolute;
    bottom: 10px;
    right: 10px;
    background: rgba(0, 0, 0, 0.4);
    padding: 10px;
    border-radius: 10px
}

#position span {
    color: white;
}

#chat-form {
    width: 100%;
    background: black;
    float: left;
    bottom: 0px;
    border-radius: 5px;
    display: none;
    /*margin-top: 15px;
    position: absolute;
    bottom: 0px;*/

    width: 400px;
}

#chat-form label {
    color: white;
    float: left;
    margin: 5px;
}

#chat-input {
    background: black;
    width: 80%;
    border: none !important;
    color: white;
    padding: 5px;
    border: 1px solid grey;
    border-radius: 5px;
    float: left;
}

#chat-text {
    /*overflow-y: overlay;*/
    max-height: 200px;
    min-height: 150px;
    overflow:auto;
}

.chat-window {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100%;
}

.chat-messages {
    -webkit-flex: 1 1;
            flex: 1 1;
    height: 100%;
    overflow: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
}

#chatdiv p b {
    color: yellow;
    cursor: pointer;
}

#gameDiv {
    display: none;
}

#ctx {
    position: absolute;
    top: 8px;
    left: 8px;
    border: 1px solid black
}

#ctx, .leftni {
    float: left;
}

#ctx-ui {
    position: absolute;
    top: 8px;
    left: 8px;
    border: 1px solid black
}

#ui {
    position: absolute;
    top: 8px;
    left: 8px;
}

#signDiv {
    width: 400px;
    min-height: 200px;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -200px;
    margin-top: -150px;
    border: 1px solid black;
    border-radius: 10px;
    padding: 10px 20px;
    background: #333;
    color: #eee;
}

.leftni {
    margni-left: 30px;
    width: 300px;
}

